.Details {
    display: flex;
    flex: auto;
}

.column {
    display: flex;
    flex-direction: column;
    padding-left: 30px;
    text-align: left;
}

h2{
    font-weight: 550;
    padding-top: 30px;
    padding-bottom: 15px;
    margin: 0;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 3em;
    color: black;
    user-select: none;
  }

.Details h3{
    font-weight: 550;
    padding-top: 30px;
    padding-bottom: 20px;
    margin: 0;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: em;
    color: black;
    user-select: none;
  }

h4{
    font-weight: 100;
    padding-bottom: 30px;
    margin: 0;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 25px;
    color: black;
    user-select: none;
  }